<script setup lang="ts">
import { ref, onMounted, inject } from 'vue'
import InputText from 'primevue/inputtext'
import Button from 'primevue/button'
import { useWaniKaniStore } from '@/stores/waniKani'
const waniKaniStore = useWaniKaniStore()
const notify = inject('notify')

onMounted(() => {})
const load = () => {
  // waniKaniStore.fetchUserData()
  // waniKaniStore.fetchSubjects()
  //@ts-ignore
  notify.promise(
    () => waniKaniStore.fetchSubjects().then(() => {}),
    'Loading WaniKani Subjects',
    'WaniKani Subjects Loaded',
    'WaniKani Subjects Load Failed'
  )
  //@ts-ignore
  notify.promise(
    () => waniKaniStore.fetchUserData().then(() => {}),
    'Loading WaniKani User Data',
    'WaniKani User Data Loaded',
    'WaniKani User Data Failed'
  )
}
</script>

<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <label for="txtApiKey">API Key</label>
        <InputText v-model="waniKaniStore.apiKey"></InputText>
        <Button @click="load">Load</Button>
      </div>
    </div>
    <div class="row">
      <div class="col">
        {{ waniKaniStore.userData }}
      </div>
    </div>
    <div class="row">
      <div class="col">
        {{ waniKaniStore.subjects }}
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss"></style>
